* {
  margin: 0;
  padding: 0;
  color: rgb(34, 34, 34);
}

.container {
  max-width: 100rem;
  margin: 30px auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 0.8fr 1.6fr 0.6fr;
  grid-template:
    "image general_data"
    "description contact"
    "location location";
  grid-gap: 18px;
}

.image {
  grid-area: image;
  min-width: 550px;
  background-color: rgb(241, 241, 241);
  border-radius: 15px;
  box-shadow: 7px 7px 10px rgba(0, 0, 0, 0.397);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-left: 20px;
  margin-right: 20px;
}

.image_img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.general_data {
  grid-area: general_data;
  min-width: 350px;
  background-color: rgb(241, 241, 241);
  border-radius: 15px;
  box-shadow: 10px 5px 1rem rgba(0, 0, 0, 0.267);
}

.general_data_p {
  padding-left: 50px;
  padding-right: 50px;
  padding-bottom: 10px;
  padding-top: 10px;
  font-size: 18px;
}

.description {
  grid-area: description;
  background-color: rgb(241, 241, 241);
  border-radius: 15px;
  box-shadow: 10px 5px 1rem rgba(0, 0, 0, 0.267);
}

.description_li {
  list-style-type: none;
}

.description_p {
  padding-left: 25px;
  padding-right: 50px;
  padding-bottom: 10px;
  padding-top: 10px;
  font-size: 18px;
}

.contact {
  background-color: rgb(241, 241, 241);
  border-radius: 15px;
  box-shadow: 10px 5px 1rem rgba(0, 0, 0, 0.267);
  grid-area: contact;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.contact_p {
  color: black;
  font-size: 30px;
  font-weight: 600;
  text-shadow: 10px 5px 0.5rem rgba(0, 0, 0, 0.267);
}

.location {
  grid-area: location;
  background-color: rgb(241, 241, 241);
  border-radius: 15px;
  box-shadow: 10px 5px 1rem rgba(0, 0, 0, 0.267);
  padding: 20px;
}

.location_p_header {
  color: black;
  font-size: 30px;
  font-weight: 600;
  text-align: center;
  text-shadow: 10px 5px 0.5rem rgba(0, 0, 0, 0.267);
}

.location_p {
  font-size: 17px;
  font-size: 18px;
  text-align: center;
  padding-top: 15px;
}

